<template>
  <div class="add_container">
      <head-top head-title="新增地址" goBack="true"></head-top>
      <section class="adddetail">
          <form v-on:submit.prevent>
              <section class="ul-padding-block">
                  <div class="input-new">
                      <input type="text" placeholder="请填写你的姓名" :class="{verifies:verify}" v-model="message" @input="inputThing">
                      <p v-if="verify">请填写你的姓名</p>
                  </div>
                    <router-link  to="/profile/info/address/add/addDetail" class="addde-tail">
                        <div class="input-new">
                            <!-- readonly只读 -->
                            <input type="text" placeholder="小区/写字楼/学校等" readonly="readonly" v-model="addAddress" />
                        </div>
                    </router-link>
                    <div class="input-new">
                        <input type="text" placeholder="请填写详细的送产地址" :class="{verifies:verifythree}" @input="inputThingthree" v-model="mesthree">
                        <p v-if="verifythree">{{sendaddress}}</p>
                    </div>
                    <div class="input-new">
                        <input type="text" placeholder="请填写能够联系到您的手机号" :class="{verifies:verifyfour}" v-model="telenum" @input="inputThingfour">
                        <p v-if="verifyfour">{{telephone}}</p>
                    </div>
                    <div class="input-new">
                        <input type="text" placeholder="备用联系电话（选填）" v-model="standbytelenum" @input="inputThingfive">
                        <p v-if="verifyfive">{{standbytele}}</p>
                    </div> 
              </section>
              <section class="addbutton">
                  <button :class="{butopacity:butpart}" @click.prevent="submitThing">新增地址</button>
              </section>
          </form>
      </section>
      <alert-tip v-if="showAlert" @closeTip="showAlert = false" :alertText="alertText"></alert-tip>
      <transition mode="out-in" name="add-view">
          <router-view></router-view>
      </transition>
  </div>
</template>

<script>
import headTop from '../../../../../components/header/header'
import {mapState,mapMutations} from 'vuex'
import alertTip from '../../../../../components/common/alertTip'
import {postAddAddress} from '../../../../../service/getData'
import {getImgPath} from '../../../../../components/common/mixin'
export default {
    data () {
        return {
            verify:false,//姓名
            verifytwo:false,//备注
            verifythree:false,//地址
            verifyfour:false,//电话
            verifyfive:false,//备用电话
            butpart:false,//新增地址按钮的透明度
            sendaddress:'',//地址
            message:'',//信息
            mesthree:'',//送餐地址
            telephone:'',//手机号提示
            telenum:'',//手机号
            standbytele:'',//备用手机号提示
            standbytelenum:'',//备用手机号
            addSearn:false,//添加搜索地址
            newAddress:{},//增加数组的元素
            showAlert:false,
            alertText:null
        }
    },components: {
        headTop,
        alertTip
    },computed: {
        ...mapState([
            'userInfo','addAddress','removeAddress','geohash','newAddress'
        ])
    },methods: {
        ...mapMutations([
          'ADD_ADDRESS'  
        ]),
        inputThing()
        {
            (!this.message) ? this.verify=true : this.verify=false;
            this.bindThing();
        },
        inputThingthree()
        {
            if(!this.mesthree)
            {
                this.verifythree=true;
                this.sendaddress='请填写送餐地址';
            }else if(this.mesthree.length<5)
            {
                this.verifythree=true;
                this.sendaddress='送餐地址太短,不能识别';
            }else{
                this.verifythree=false;
            }
            this.bindThing();
        },
        inputThingfour()
        {
            this.verifyfour=true;
            if((/^[1][358][0-9]{9}$/).test(this.telenum))
            {
                this.verifyfour=false;
            }else if(this.telenum=='')
            {
                this.telephone='手机号不能为空';
            }else{
                this.telephone='请填写正确的手机号码';
            }
             this.bindThing();
        },
        inputThingfive()
        {
            this.verifyfive=true;
            if((/^[1][358][0-9]{9}$/).test(this.standbytelenum) || this.standbytelenum=='')
            {
                this.verifyfive=false;
            }else{
                this.standbytele='请填写正确的手机号码';
            }
        },
        bindThing()
        {
            if(this.message && this.mesthree && !this.verifyfour)
            {
                this.butpart=true;
            }else{
                this.butpart=false;
            }
        },
        submitThing()
        {
            console.log();
            if(this.userInfo && this.userInfo.user_id)
            {
                console.log(!(this.verifyfour))
                if(this.verifyfour || this.verifythree || this.verify || this.addAddress=='')
                {
                    this.alertText="请填写信息";
                    this.showAlert=true;
                }else{
                    let res=postAddAddress(this.userInfo.user_id,this.mesthree, this.addAddress, this.geohash, this.message, this.telenum, this.standbytelenum, 0, 1, '公司', 4);
                    if(res.message)
                    {
                        this.alertText=res.message;
                        this.showAlert=true;
                    }
                    //把新地址存入vuex
                    this.ADD_ADDRESS({
                         name: this.message,
                        address: this.mesthree,
                        address_detail: this.addAddress,
                        geohash: 'wtw37r7cxep4',
                        phone: this.telenum,
                        phone_bk: this.standbytelenum,
                        poi: this.addAddress,
                        poi_type: 0,
                    })
                    this.$router.go(-1);
                }
            }
        }
    }
}
</script>

<style lang="scss" scoped>
@import '../../../../../style/mixin.scss';
.add_container{
    position: fixed;
    top:0;
    left:0;
    bottom:0;
    right:0;
    background: #f5f5f5;
    z-index: 250;
    p, span{
        font-family: Helvetica Neue,Tahoma,Arial;
    }
}
.adddetail{
    margin-top: 2.3rem;
    form{
        .ul-padding-block{
            background: #fff;
            padding:0.4rem;
            .input-new{
                .verifies{
                    border-color:Red;
                }
                p{
                    @include sc(0.5rem,red);
                    margin-top: 0.3rem;
                }
                margin-top: 0.3rem;
                input{
                    background: #f2f2f2;
                    border:1px solid #ddd;
                    @include wh(100%,1.4rem);
                    border-radius: 0.2rem;
                    @include sc(0.6rem,#333);
                    padding:0.2rem;
                }
            }
        }
    }
}
.addbutton{
    margin:0.5rem;
    button{
        @include wh(100%,2rem);
        background: #4cd964;
        @include sc(0.7rem,#fff);
    }
}
.add-view-enter-active,.add-view-leave-active{
    transition: all .4s;
}
.add-view-enter,.add-view-leave-active{
    transform: translate3d(2rem,0,0);
    opacity: 0;
}
</style>



